
.btn-square {
    height: 30px;
    width:  30px ;
    border-radius: 20px;
    vertical-align: center !important;
    text-align: center;
    display: inline-block;
}

.btn-circle {
    border-radius: 20px;
    height: 40px;
    width:  40px ;

}

.circle-base {
    border-radius: 50%;
    text-align: center;
    font-weight: bold;
    padding-left: initial !important;
    padding-right: initial !important;
}
.circle-base:hover {
    cursor: pointer;
}


.circle-base span {

    /*vertical-align: middle;*/
}

.circle-light {
    width: 30px;
    height: 30px;
    background: white;
    border: 2px solid black;
    color: black;
}
.circle-dark {
    width: 30px;
    height: 30px;
    background: black;
    border: 3px solid #000;
    color: white;
}
.type3 {
    width: 500px;
    height: 500px;
    background: aqua;
    border: 30px solid blue;
}
